<template>
  <div class="banner">
    <div class="container-fluid banner d-flex align-items-center p-0">
      <div class="d-flex align-items-center">
        <div class="desktop-banner d-none d-md-block">
          <span v-html="$t('marquee.event.banner')"> </span>
        </div>

        <div class="ticker-wrap d-md-none d-block">
          <div class="ticker">
            <span class="item-collection-1">
              <span v-html="$t('marquee.event.banner')" class="item"> </span>
              <span v-html="$t('marquee.event.banner')" class="item"> </span>
            </span>
            <span class="item-collection-2">
              <span v-html="$t('marquee.event.banner')" class="item"> </span>
              <span v-html="$t('marquee.event.banner')" class="item"> </span>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      before: false,
      after: false,
      during: false,
    };
  },
  methods: {
    getTime() {
      let options = {
          timeZone: "Europe/Rome",
          year: "numeric",
          month: "numeric",
          day: "numeric",
        },
        formatter = new Intl.DateTimeFormat([], options);
      const currentDate = formatter.format(new Date());
      const startDate = formatter.format(new Date("2022-09-17"));
      const endDate = formatter.format(new Date("2022-09-18"));

      let hours = new Date().getUTCHours() + 2;

      if (currentDate >= startDate && currentDate <= endDate) {
        if (currentDate === startDate) {
          if (hours >= 14 && hours <= 23) {
            this.during = true;
            this.before = false;
          } else {
            this.before = true;
          }
        } else if (currentDate === endDate) {
          if (hours >= 14 && hours <= 17) {
            this.during = true;
            this.before = false;
          } else {
            this.during = false;
            this.before = false;
            this.after = true;
          }
        }
      } else {
        this.during = false;
        this.before = false;
        this.after = true;
      }
    },
  },

  created() {
    // this.getTime();
  },
};
</script>

